html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.App {
  max-width: 320px;
  margin: 0 auto;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-logo {
  width: 50%;
}

.App-intro {
  font-size: large;
}

.inner-container {
  /* background: rgb(117, 110, 110); */
  border-radius: 5px;
  margin-bottom: 1rem;
  float: left;
  width: 100%;
}

.inner-container:first-child {
  padding: 1rem;
}

.score--high {
  float: right;
}

.form-container {
  padding-top: 1rem;
}

.container-lottie {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
}

.App-lottie {
  position: absolute;
  top: -10%;
  right: -10%;
  left: -10%;
  bottom: -10%;
}